<template>
  <div>
    <v-btn icon v-if="!$store.state.currentUser" to="/account">
      <v-icon size="40">mdi-account-circle</v-icon>
    </v-btn>
    <!-- offset-y 在y轴上偏移菜单， 与nudge-bottom 、 nudge-left 微调配合使用 -->
    <!-- transition: 动画效果，与 bottom、 left、 origin="right top" 配合使用 -->
    <v-menu
      v-else
      offset-y
      nudge-bottom="8"
      nudge-left="15"
      transition="scale-transition"
      bottom
      left
      origin="right top"
    >
      <!-- template 下拉菜单默认显示的内容，比如按钮 -->
      <template v-slot:activator="{ on, attrs }">
        <v-btn icon v-bind="attrs" v-on="on">
          <v-avatar size="36">
            <img :src="$store.state.currentUser.avatar" />
          </v-avatar>
        </v-btn>
      </template>
      <!--  </template> 与 </v-menu> 之间放置下拉菜单弹出的内容 -->
      <v-list>
        <v-list-item link to="/dashboard/userinfo">
          <v-list-item-action>
            <v-icon>mdi-account-settings-outline</v-icon>
          </v-list-item-action>
          <v-list-item-title>个人信息</v-list-item-title>
        </v-list-item>

        <v-list-item link>
          <v-list-item-action>
            <v-icon>mdi-gantry-crane</v-icon>
          </v-list-item-action>
          <v-list-item-title>修改密码</v-list-item-title>
        </v-list-item>

        <v-list-item link @click="logout">
          <v-list-item-action>
            <v-icon>mdi-power</v-icon>
          </v-list-item-action>
          <v-list-item-title>退出</v-list-item-title>
        </v-list-item>
      </v-list>
    </v-menu>
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";
import AccountService from "@/service/account_service";
export default {
  name: "user-status",
  computed: {
    // 把状态中定义的currentUser 映射为当前组件同名的计算属性（当变量用s）
    ...mapState(["currentUser"]),
  },
  mounted() {
    // this.SET_CURRENT_USER(AccountService.currentUser());
    // console.log("show currnet user: ", this.$store.state.currentUser);
  },
  methods: {
    /// ... 在此处为解构操作，用于将mapMutations中的对象展开
    ...mapMutations(
      // 把 状态中定义的方法名："SET_CURRENT_USER" 映射为当前组件的同名方法，
      ["SET_CURRENT_USER"]
    ),

    logout() {
      AccountService.logOut()
        .then((resp) => {
          this.SET_CURRENT_USER(resp);
          console.log(resp);
          if (this.$route.path != "/") {
            this.$router.push("/");
          }
        })
        .catch((error) => {
          console.log(error);
        });
    },
  },
};
</script>

